import axios from "../axios";

// 保存
export const send = data => {
  return axios({
    url: "/sendUser",
    method: "get",
    data
  });
};

//  广播，服务器主动推给连接的客户端
export const sendTopic = data => {
  return axios({
    url: "/sendTopic",
    method: "get",
    data
  });
};

// 像所有人说话
export const sendAllUser = data => {
  return axios({
    url: "/sendAllUser",
    method: "post",
    data
  });
};

// 像指定人说话
export const sendMyUser = data => {
  return axios({
    url: "/sendMyUser",
    method: "post",
    data
  });
};