import axios from '../axios'

/* 
 * 机构管理模块
 */

// 保存
export const save = (data) => {
  return axios({
    url: '/org/save',
    method: 'post',
    data
  })
}

// 删除
export const batchDelete = (data) => {
  return axios({
    url: '/org/delete',
    method: 'post',
    data
  })
}
// 分页查询
export const findPage = (data) => {
  return axios({
    url: '/org/findPage',
    method: 'post',
    data
  })
}

// 查询机构菜单集合
export const findOrgMenus = (params) => {
  return axios({
    url: '/org/findOrgMenus',
    method: 'get',
    params
  })
}

//保存架构菜单
export const saveOrgMenus = (data) => {
  return axios({
    url: '/org/saveOrgMenus',
    method: "post",
    data
  })
}