import axios from "../axios";

/*
 * 用户管理模块
 */

// 保存
export const save = data => {
  return axios({
    url: "/user/save",
    method: "post",
    data
  });
};

// 修改个人信息
export const updatePersonal = data => {
  return axios({
    url: "/user/updatePersonal",
    method: "post",
    data
  });
};
// 切换用户状态
export const userStatusSwitching = data => {
  return axios({
    url: "/user/userStatusSwitching",
    method: "post",
    data
  });
};

// 查询用户详情信息
export const findByName = params => {
  return axios({
    url: "/user/findByName",
    method: "get",
    params
  });
};


// 删除
export const batchDelete = data => {
  return axios({
    url: "/user/delete",
    method: "post",
    data
  });
};
// 分页查询
export const findPage = data => {
  return axios({
    url: "/user/findPage",
    method: "post",
    data
  });
};
//查询用户角色信息
export const findUserRoles = params => {
  return axios({
    url: "/user/findUserRoles",
    method: "get",
    params
  })
}
// 查找用户的菜单权限标识集合
export const findPermissions = params => {
  return axios({
    url: "/user/findPermissions",
    method: "get",
    params
  });
};

// 更新用户密码
export const updatePassword = (params) => {
  return axios({
    url: '/user/updatePassword',
    method: 'get',
    params
  })
}

// 下载用户头像
export const download = (data) => {
  return axios({
    url: '/download?fileId=' + data,
    responseType: 'arraybuffer',
    method: 'get',
  }).then(function (response) { // 将后台的图片二进制流传华为base64
    return 'data:image/png;base64,' + btoa(
      new Uint8Array(response).reduce((data, byte) => data + String.fromCharCode(byte), '')
    );
  })
}